package widgets

import game.Guess
import game.GuessResult
import kotlinx.browser.document
import kotlinx.html.button
import kotlinx.html.dom.append
import kotlinx.html.dom.create
import kotlinx.html.id
import kotlinx.html.js.div
import kotlinx.html.js.onClickFunction
import kotlinx.html.style
import org.w3c.dom.HTMLDivElement
import toPx

class HowToPage {
    var returnButtonFunction = {}


    fun generate(width: Double, topHeightToWidth: Double): HTMLDivElement {
        val topPartHeight = width*topHeightToWidth
        return document.create.div {
            id ="howToDiv"

        }.also {
            it.append(generateTopPart(topPartHeight))
            it.append(generateText(topPartHeight))
            it.append{
                div{ // top row
                    id= "buttonsRow"
                    style ="""
                            margin: 5%;
                            height: ${toPx(topPartHeight)};
                        """
                }.also {
                    val extra = if(document.URL.contains("localhost")){
                        ""
                    }else{
                        "/sozck"
                    }
                    it.append {
                        val buttonName = listOf("settings","stats","replay","howto").reversed()
                        val buttonText = listOf("Nasıl Oynarım","Yeni Sözcük","İstatistikler","Ayarlar")
                        for (i in 0 until 4){
                            div{
                                style="""
                                    width: ${toPx(width*0.9*0.25)};
                                    float: left;
                                """.trimIndent()

                            }.also {
                                it.append{
                                    div{
                                        style = """
                                            opacity: 1;
                                            width:  ${toPx(topPartHeight * 0.8)};
                                            border: none;
                                            height: ${toPx(topPartHeight * 0.8)};
                                            margin-top: ${toPx(topPartHeight * 0.1)};
                                            margin-left: auto;
                                            margin-right: auto;
                                            background-image: url("${extra}/icon/${buttonName[i]}.png");
                                            background-repeat: no-repeat;
                                            background-size: ${toPx(topPartHeight * 0.7)} ${toPx(topPartHeight * 0.7)};
                                            background-position: center;
                                            float: top;
                                        """
                                    }

                                    div{
                                        style = """
                                        float: top;
                                        height:  ${toPx(topPartHeight*0.6)};
                                        width: ${toPx(0.9*width*0.25)};
                                        text-align: center;
                                        font-family: openSans, sans-serif;
                                        font-size: ${toPx(topPartHeight*0.25)};
                                        text-align: center;
                                    """
                                        text(buttonText[i])
                                    }
                                }
                            }

                        }


                    }
                }
            }
        }
    }



    fun generateText(topPartHeight:Double): HTMLDivElement {
        val boxHeight = topPartHeight*0.8
        return document.create.div{ // first paragraph
            style="""
                            text-align: left;
                            margin-left: 5%;
                            margin-right: 5%;
                            font-family: openSans, sans-serif;
                            font-size: ${toPx(topPartHeight*0.25)};
                        """.trimIndent()
        }.also {
            it.append{
                div {  }.also {
                    it.innerHTML="<p>6 denemede <span style=\"font-family:titleFont\">Sözck</span>'ü tahmin et.</p>" +
                            "<p>Tahminler geçerli bir sözcük olmalı. Dene tuşuna basarak tahminini gönder.</p>" +
                            "<p>Her tahminden sonra harflerin rengi o sözcükte bulundukları konuma göre boyanarak tahmininin ne kadar doğru olduğunu gösterir.</p>"
                }
                div{}.also {
                    it.append(generateExampleGrid(boxHeight,1))
                }
                div {
                    style = """
                                clear: both;
                                padding-top: ${toPx(topPartHeight*0.1)};
                                """
                }.also {
                    it.innerHTML="<p><span style=\"font-family:openSansBold\">D</span> harfi sözcükte var ve yeri de doğru.</p>"
                }
                div{}.also {
                    it.append(generateExampleGrid(boxHeight,2))
                }

                div {
                    style = """
                                clear: both;
                                padding-top: ${toPx(topPartHeight*0.1)};
                                """
                }.also {
                    it.innerHTML="<p><span style=\"font-family:openSansBold\">R</span> harfi sözcükte var ama yeri yanlış.</p>"
                }

                div{}.also {
                    it.append(generateExampleGrid(boxHeight,3))
                }

                div {
                    style = """
                                clear: both;
                                padding-top: ${toPx(topPartHeight*0.1)};
                                """
                }.also {
                    it.innerHTML="<p><span style=\"font-family:openSansBold\">M</span> harfi sözcükte yer almıyor</p><p></p>" +
                            "<p>Meydan Oku tuşunu kullanarak oynadığın sözcüğü arkadaşlarına gönderebilir ve onlara karşı yarışabilirsin.</p>"
                }

            }
        }
    }


    fun generateTopPart(topPartHeight: Double): HTMLDivElement {
        return document.create.div{ // top row
            id= "howToTopRow"
            style ="""
                            width: 100%;
                            height: ${toPx(topPartHeight)};                     
                        """
        }.also {
            it.append {
                div{
                    id="titleDiv"
                    style =
                        """
                                        height:  ${toPx(topPartHeight)};
                                        text-align: left;
                                        margin-left: 5%;
                                        float: left;
                                        font-family: guessFont, sans-serif;
                                        font-size: ${toPx(topPartHeight*0.7)};
                                        text-align: center;
                                        line-height: ${toPx(topPartHeight)};
                                    """

                    text("Nasıl Oynanır")
                }

                div{
                    id="titleDiv"
                    style =
                        """
                                float: right;
                                height:  ${toPx(topPartHeight)};
                                width:  ${toPx(topPartHeight)};
                                text-align: center;
                                margin-top: auto;
                                """
                }.also {
                    it.append{
                        val extra = if(document.URL.contains("localhost")){
                            ""
                        }else{
                            "/sozck"
                        }

                        button{
                            onClickFunction = {event->
                                returnButtonFunction()
                            }
                            style = """
                                            opacity: 1;
                                            width:  ${toPx(topPartHeight * 0.8)};
                                            border: none;
                                            height: ${toPx(topPartHeight * 0.8)};
                                            margin-top: ${toPx(topPartHeight * 0.1)};
                                            background-image: url("${extra}/icon/replay.png");
                                            background-repeat: no-repeat;
                                            background-size: ${toPx(topPartHeight * 0.5)} ${toPx(topPartHeight * 0.5)};
                                            background-position: center;
                                        """
                        }
                    }
                }
            }
        }
    }



    fun generateExampleGrid(height: Double, exampleNo: Int): HTMLDivElement {
        val dd = document.create.div {
            style = """
                float: left;
                margin-left: 10%;
            """.trimIndent()
        }

        val letters: List<String>
        val targetLetter: Int
        val targetColor: String
        when(exampleNo){
            1-> {
                letters = listOf("D","A","M","L","A")
                targetLetter = 0
                targetColor = GuessResult.RIGHT_PLACE.colour()
            }
            2->{
                letters = listOf("A","R","A","B","A")
                targetLetter = 1
                targetColor = GuessResult.WRONG_PLACE.colour()
            }
            3->{
                letters = listOf("D","E","M","İ","R")
                targetLetter = 2
                targetColor = GuessResult.WRONG.colour()
            }
            else -> {
                letters = listOf("D","A","M","L","A")
                throw Exception("this shouldnt happen")
            }
        }

        for(i in 0 until 5){
            val d = document.create.div {
                style = """
                        border: solid;
                        opacity: .8;
                        width: ${toPx(height)};
                        height: ${toPx(height)};
                        float: left;
                        box-sizing: border-box;
                    """
            }

            document.create.div {
                text(letters[i])
                style = """
                            font-family: guessFont, sans-serif;
                            font-size: ${toPx(height * 0.7)};
                            text-align: center;
                            line-height: ${toPx(height*0.8)};
                            margin: 5%;
                            width: 90%;
                            height: 90%;
                        """

            }.also {
                if(i==targetLetter){
                    it.style.backgroundColor=targetColor
                }
                d.append(it)
            }
            dd.append(d)
        }


        return dd
    }

}