        document.bgColor = color
    }
}

fun challenge(game: Game) {
    println(game.createChallenge())
}




fun Node.sayHello(game: Game) {

    midBanner.challenge = { challenge(game) }

    val ultra = topPart.heightToWidth + keyboard.heightToWidth + midBanner.heightToWidth + adBanner.heightToWidth
    val sc = SizeCalculator(ultra,game)
    val howToPage = HowToPage()


    val gridPart = GridPart(
        sc.appWidth.toDouble(),
        sc.gridHeight,
        Constants.attempts,
        Constants.letterLimit
    )

    keyboard.letterPressed = { c ->
        if (game.playable) {
            game.addLetter(c)
            gridPart.writeLetter(game.recordedWord)
        }

    }

    keyboard.erasePressed = {
        if (game.playable) {
            game.removeLetter()
            gridPart.writeLetter(game.recordedWord)
        }
    }

    keyboard.enterPressed = {
        if (game.playable) {



            val res = game.checkWord()
            if(res.warningNeeded){
                document.getElementById("main")!!.append(popup(res.warningText))
            }


            gridPart.paintRow(game.guessDatabase)
            keyboard.paintKeys(game.guessDatabase,game.letters)
            if (game.gameSolved) {
                val time = Constants.seconds
                timerActive = false
                window.alert("Cevap bulundu, yürü be.")
                game.stats.played++
                game.stats.win++
                game.stats.streak++
                if (game.stats.streak > game.stats.maxStreak) game.stats.maxStreak = game.stats.streak
                if (time < game.stats.timeRecord) game.stats.timeRecord = time
                game.stats.distribution[game.activeRow]++
                val names = listOf("played", "win", "streak", "maxStreak", "record", "d1", "d2", "d3", "d4", "d5", "d6")
                val values = mutableListOf(
                    game.stats.played.toString(),
                    game.stats.win.toString(),
                    game.stats.streak.toString(),
                    game.stats.maxStreak.toString(),
                    game.stats.timeRecord.toString()
                )
                game.stats.distribution.forEach { values.add(it.toString()) }
                generateCookies(names, values)
                // TODO game solved
                println(document.cookie)
            }
            if (!game.gameSolved && game.activeRow == 6) {
                Constants.seconds = 0
                timerActive = false
                window.alert("Başaramadınız. Kelime= ${game.gameWord}")
                game.stats.played++
                game.stats.streak = 0
                val names = listOf("played", "streak")
                val values = mutableListOf(game.stats.played.toString(), game.stats.streak.toString())
                generateCookies(names, values)
                println(document.cookie)
            }
            if(game.gameLost){
                window.alert("cevap ${game.gameWord}. Bir dahakine artık.")
            }
            gridPart.activeRow = game.activeRow
            gridPart.moveTriangle()
        }
    }

    topPart.replayFunction = {
        game.startRound()
        game.recordedWord = ""
        keyboard.resetKeys()
        gridPart.clear()
        gridPart.activeRow = game.activeRow
        gridPart.moveTriangle()
        Constants.seconds = 0
        midBanner.updateTimer("00:00")
        timerActive = true
    }

    val gameDiv = gameDiv(gridPart,sc)
    val howToDiv = howToPage.generate(sc.appWidth.toDouble(),Constants.topPart.heightToWidth)
    howToDiv.hidden=true

    topPart.howToFunction = {
        gameDiv.hidden = true
        howToDiv.hidden = false
    }

    howToPage.returnButtonFunction = {
        howToDiv.hidden = true
        gameDiv.hidden = false
    }





    append {
        div {
            id = "background"
            style="""
            background-color: ${Constants.outsideColour};
            position: absolute;
            left : 0px;
            top : 0px;
            width : ${toPx(window.innerWidth)};
            height : ${toPx(window.innerHeight)};                        
        """
        }

        div {
            id = "main"
        }.also {
            it.style.let { style ->
                style.color = Constants.textColor
                style.margin = "auto"
                style.width = toPx(sc.appWidth)
                style.height = toPx(window.innerHeight - 20)
                style.backgroundColor = Constants.backgroundColor
                style.position = "relative"
            }

            it.append (gameDiv)



            it.append(
                howToDiv
            )


        }
    }

    window.setInterval(handler = { updateTimer() }, 1000)
}

fun gameDiv(gridPart: GridPart, sc: SizeCalculator): HTMLDivElement {
    return document.create.div{
        id = "gameDiv"
    }.also {
        it.append {
            div {
                id = "topPart"
            }.also {
                it.append(topPart.generate(sc.appWidth))
            }



            div {
                id = "grid"
            }.also {
                it.append(gridPart.createGrid())
            }


            div {
                id = "commercial"
                style = """
                        margin: auto;
                        width: 100%;
                        position: absolute;
                        bottom: ${toPx(keyboard.expectedHeight(sc.appWidth) + midBanner.expectedHeight(sc.appWidth))};
                        left: 0;
                        background-color: #44FF44;
                    """
            }.also {
                it.append(adBanner.generate(sc.appWidth))
            }


            div {
                id = "midBannerBox"
                style = """
                        width: 100%;
                        position: absolute;
                        bottom: ${toPx(keyboard.expectedHeight(sc.appWidth))};
                        left: 0px;
                    """
            }.also {
                it.append(midBanner.generate(sc.appWidth))
            }




            div {
                id = "keyboardBox"
                style = """
                        width: 100%;
                        position: absolute;
                        bottom: 0px;
                        left: 0px;                        
                    """
            }.also {
                it.append(keyboard.generate(sc.appWidth))
            }
        }
    }
}


fun popup(text: String): HTMLDivElement {
    val d = document.create.div {
        id = "popper"
        style = """
                        position: absolute;
                        top: 10%;
                        left: 0px;
                        width: 100%;
                        animation-name: popWarning;
                        animation-delay: ${Constants.popupDelay};
                        animation-duration: ${Constants.popupRoll};
                        animation-fill-mode: forwards;
                    """

    }
    d.append(
        document.create.div {
            text(text)
        }.also {
            it.className="challengeButton"
            it.style.padding = "20px"
            it.style.marginLeft="auto"
            it.style.marginRight="auto"
            it.style.display="block"
            it.style.width = "50%"
        }
    )

    d.addEventListener("animationend",{
        d.remove()
    })
    return d
}




fun toPx(v: Double): String {
    return v.toInt().toString() + "px"
}

fun toPx(v: Int): String {
    return v.toString() + "px"
}